body {
  background-color: #bebebe;
}

.c {
  text-align: center;
}
h1 {
  margin: 0;
}
.mybutton {
  background-color: blue;
  color: white;
  padding: 10px 20px;
  border-radius: 10px;
  box-shadow: 5px 5px 8px #888888;
}

.mybutton:hover {
  background-color: red;
}

h1 {
  height: 250px;
}
.card {
  background-color: #add8e5;
  margin: auto;
  margin-top: 10px;
  width: 500px;
  padding: 20px;
  color: #1b3e59;
  border-radius: 15px;
  box-shadow: 8px 8px 16px 2px #1b3e59;
}

a:hover {
  box-shadow: inset 300px 0 0 0 blue;
  color: white;
}
